<template>
  <div>
    <v-overlay :value="set_overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <div class="pt-6 text_color mobileview">
      {{ $t("newUi.adminViewform.addPro") }}
    </div>
    <v-container class="mobileview padding_view">
      <v-text-field
        v-bind:label="$t('newUi.addItem.itemName')"
        v-model="addproduct_name"
        v-bind:title="$t('')"
        autocomplete="off"
        required
      ></v-text-field>
      <v-textarea
        v-bind:label="$t('newUi.addItem.enterDesc')"
        v-model="addproduct_desc"
        autocomplete="off"
        v-bind:title="$t('')"
        required
        name="text"
        row="2"
        row-height="20"
      ></v-textarea>
      <v-text-field
        v-bind:prefix="this.$store.state.currentForm.symbol"
        v-bind:label="$t('newUi.addItem.price')"
        v-model="addproduct_price"
        v-bind:title="$t('')"
        autocomplete="off"
        required
        type="number"
        min="0"
      ></v-text-field>
      <v-select
        v-bind:label="$t('newUi.addItem.category')"
        v-model="addproduct_category"
        :items="category_name"
      ></v-select>
      <v-avatar
        class="ma-3 text-center mobileview cursor-pointer upload-image-box"
        size="100"
        tile
      >
        <v-img v-if="addproduct_image && !isEditable" :src="addproduct_image" />
        <v-card
          v-if="!addproduct_image && isEditable"
          @click="uploading('mobile')"
          flat
          tile
          class="text-center mx-auto border"
          style="padding-top: 20px; height: 100px; width: 100px"
        >
          <img
            class="cursor-pointer plus_icon"
            src="../assets/ui/plus_green.svg"
            alt=""
          />
          <div class="text-center text_color">
            {{ $t("newUi.addItem.upload") }}
          </div>
        </v-card>
        <v-img
          v-if="addproduct_image && isEditable"
          @click="uploading('mobile')"
          :src="addproduct_image"
        ></v-img>
      </v-avatar>
      <div class="text-center mb-4 upload-image-buttons">
        <v-btn
          @click.prevent="
            add_product_submit(
              addproduct_name,
              addproduct_desc,
              addproduct_price,
              addproduct_category,
              addproduct_image
            )
          "
          class="button_padding mt-2"
          color="#8AB26D"
        >
          {{ $t("newUi.editForm.save") }}
        </v-btn>
        <div class="mt-2">
          <span
            @click.prevent="
              add_product_more(
                addproduct_name,
                addproduct_desc,
                addproduct_price,
                addproduct_category,
                addproduct_image
              )
            "
            class="text-decoration-underline cursor-pointer text_color"
            >{{ $t("newUi.addItem.saveMore") }}</span
          >
        </div>
      </div>
    </v-container>

    <!-- Desktopview -->
    <v-container class="desktopView mt-5 row padding_view">
      <div class="col">
        <v-text-field
          v-bind:label="$t('newUi.addItem.itemName')"
          v-model="addproduct_name"
          v-bind:title="$t('')"
          autocomplete="off"
          required
        ></v-text-field>
        <v-textarea
          v-bind:label="$t('newUi.addItem.enterDesc')"
          v-model="addproduct_desc"
          autocomplete="off"
          v-bind:title="$t('')"
          required
          name="text"
          row="2"
          row-height="20"
        ></v-textarea>
        <v-text-field
          v-bind:prefix="this.$store.state.currentForm.symbol"
          v-bind:label="$t('newUi.addItem.price')"
          v-model="addproduct_price"
          v-bind:title="$t('')"
          autocomplete="off"
          required
          type="number"
          min="0"
        ></v-text-field>
        <v-select
          v-bind:label="$t('newUi.addItem.category')"
          v-model="addproduct_category"
          :items="category_name"
        ></v-select>
      </div>

      <v-avatar class="desktopView cursor-pointer" size="100" tile>
        <v-img v-if="addproduct_image && !isEditable" :src="addproduct_image" />
        <v-card
          v-if="!addproduct_image && isEditable"
          @click="uploading('desktop')"
          flat
          tile
          class="text-center"
          style="
            padding-top: 30px;
            width: 100px;
            height: 100px;
            background: #f8faf7;
          "
        >
          <img
            class="cursor-pointer plus_icon"
            src="../assets/ui/plus_green.svg"
            alt=""
          />
          <div class="text-center text_color" style="font-size: 12px">
            {{ $t("newUi.addItem.upload") }}
          </div>
        </v-card>
        <v-img
          v-if="addproduct_image && isEditable"
          @click="uploading('desktop')"
          :src="addproduct_image"
        ></v-img>
      </v-avatar>
      <!-- <div class="mobileview row text-center mb-4">
      <v-btn  @click.prevent="add_product_submit(addproduct_name, addproduct_desc, addproduct_price, addproduct_category, addproduct_image)" class="button_padding" color="#8AB26D">Save</v-btn>
      <div>
          <span @click.prevent="add_product_more(addproduct_name, addproduct_desc, addproduct_price, addproduct_category, addproduct_image)" class="text-decoration-underline cursor text_color">Save and add more</span></div>
      </div> -->
    </v-container>
    <div class="desktopView mb-4">
      <v-btn
        @click.prevent="
          add_product_submit(
            addproduct_name,
            addproduct_desc,
            addproduct_price,
            addproduct_category,
            addproduct_image
          )
        "
        class="button_padding"
        color="#8AB26D"
      >
        {{ $t("newUi.editForm.save") }}
      </v-btn>
      <v-btn
        @click.prevent="cancel"
        class="button_padding desktopView"
        color="#8AB26D"
      >
        {{ $t("newUi.editForm.cancel") }}
      </v-btn>

      <div class="mt-2">
        <span
          @click.prevent="
            add_product_more(
              addproduct_name,
              addproduct_desc,
              addproduct_price,
              addproduct_category,
              addproduct_image
            )
          "
          class="text-decoration-underline cursor-pointer text_color"
          >{{ $t("newUi.addItem.saveMore") }}</span
        >
      </div>
    </div>

    <div id="imageCroper" class="modal">
      <div
        class="modal-content"
        style="display: flex;"
        :style="
          $vuetify.breakpoint.height > 450
            ? { 'justify-content': 'center' }
            : { 'justify-content': 'start' }
        "
      >
        <!--<cropImage />-->
        <div style="position relative">
          <vue-croppie
            ref="croppieRef"
            :enableOrientation="true"
            :enableResize="false"
            :boundary="
              $vuetify.breakpoint.height > 450
                ? { width: 300, height: 300 }
                : { width: 250, height: 250 }
            "
            :viewport="{ width: 240, height: 240 }"
            @result="result"
            @update="update"
          >
          </vue-croppie>

          <div
            class="col d-flex justify-space-between text-center text-white"
            style="background: black; position: absolute; bottom: 0"
          >
            <div
              class="text-center pt-3 cursor-pointer"
              @click="modalToggle('none')"
            >
              <i class="bi bi-chevron-left"></i>{{ $t("newUi.addItem.back") }}
            </div>
            <img
              class="cursor-pointer pt-2"
              src="../assets/ui/rotate_icon.svg"
              alt=""
              @click="rotate(-90)"
            />
            <div class="crop_icon mt-2" @click="crop(), (cropDialog = false)">
              <img
                class="cursor-pointer pt-1"
                src="../assets/ui/right_icon.svg"
                alt=""
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import router from "../router/router";
import { saveFile, setLocale } from "../services/firebase";
import { saveMasterData } from "../services/firebase";
import store from "../store/store";
import i18n from "../i18n";
import { saveImage } from "../services/firebase";
export default {
  props: ["payload"],

  data() {
    return {
      sheet: false,
      add_category: false,
      name: "",
      description: "",
      addproduct_category: i18n.t("newUi.addItem.noCate"),
      addproduct_desc: "",
      addproduct_name: "",
      addproduct_price: 0,
      addproduct_image: "",
      cropped: null,
      data_saved: false,
      cropDialog: false,
      cropDialog2: false,
      croppieElm: null,
      set_overlay: false,
    };
  },
  mounted() {
    setLocale();
    this.set_product_item_null();
  },
  computed: {
    type: function() {
      if (this.payload.type == "Ordering form") {
        return i18n.t("common.typeOrderForm");
      } else {
        return i18n.t("common.typeCatelogue");
      }
    },
    category_name: function() {
      let categories = [i18n.t("newUi.addItem.noCate")];
      let objects = this.payload.catalogue.filter((o) => o.type == "category");
      objects.forEach((item) => {
        categories.push(item.name);
      });
      return categories;
    },
    category_object: function() {
      let categories = [{ name: i18n.t("newUi.addItem.noCate"), value: "" }];
      let objects = this.payload.catalogue.filter((o) => o.type == "category");
      // for(var item = 1; item <= objects.length; item ++) {
      //   categories[item].name= objects[item].name
      //   categories[item].value= objects[item].name
      // }
      objects.forEach((item) => {
        categories.push({ name: item.name, value: item.name });
      });
      return categories;
    },
    isEditable: function() {
      if (this.$route.name === "viewform") return false;
      return true;
    },
  },
  methods: {
    modalToggle: function(display) {
      var modal = document.getElementById("imageCroper");
      modal.style.display = display;
      window.onclick = function(event) {
        if (event.target == modal) {
          modal.style.display = "none";
        }
      };
    },
    additem: function(name, description, price, category, image) {
      var p = this.$store.state.currentForm.maxlength;
      if (!p) {
        for (var i = 0; i < this.$store.state.forms.length; i++) {
          let form = this.$store.state.forms[i];
          console.log("form", form);
          if (form.link.split("/")[3] == this.$store.state.currentForm.pos) {
            // if (image != "") {
            //   this.$store.state.forms[i].image = image;
            // } else {
            this.$store.state.forms[i].image =
              "https://firebasestorage.googleapis.com/v0/b/gupshup-gipforms.appspot.com/o/files%2Flogo.png?alt=media&token=56547b3f-f39c-48cf-840f-e4bc085422a5";
            // }
          }
        }
        p = 1;
        this.$store.dispatch("setMaxLength", 1);
      } else {
        var maxlength = this.$store.state.currentForm.maxlength + 1;
        p = maxlength;
        this.$store.dispatch("setMaxLength", maxlength);
      }
      this.$store.state.currentForm.catalogue.push({
        pos: p,
        type: "item",
        name: name,
        desc: description,
        imageurl: image,
        price: price,
        qtty: 0,
        category: category,
      });

      if (
        router.currentRoute.fullPath ==
        "/admin/editform/" + this.$store.state.currentForm.pos
      ) {
        this.saveCurrentForm();
        // router.push("/admin/viewform/" + this.$store.state.currentForm.pos);
      } else if (
        router.currentRoute.fullPath ==
        "/admin/additemstoform/" + this.$store.state.currentForm.pos
      ) {
        this.saveCurrentForm();
        // router.push("/admin/viewform/" + this.$store.state.currentForm.pos);
      } else {
        console.log("else");
        this.saveCurrentFormMore();
      }
    },
    additem_more: function(name, description, price, category, image) {
      var p = this.$store.state.currentForm.maxlength;
      if (!p) {
        for (var i = 0; i < this.$store.state.forms.length; i++) {
          let form = this.$store.state.forms[i];
          if (form.link.split("/")[3] == this.$store.state.currentForm.pos) {
            // if (image != "") {
            //   this.$store.state.forms[i].image = image;
            // } else {
            this.$store.state.forms[i].image =
              "https://firebasestorage.googleapis.com/v0/b/gupshup-gipforms.appspot.com/o/files%2Flogo.png?alt=media&token=56547b3f-f39c-48cf-840f-e4bc085422a5";
            // }
          }
        }
        p = 1;
        this.$store.dispatch("setMaxLength", 1);
      } else {
        var maxlength = this.$store.state.currentForm.maxlength + 1;
        p = maxlength;
        this.$store.dispatch("setMaxLength", maxlength);
      }
      this.$store.state.currentForm.catalogue.push({
        pos: p,
        type: "item",
        name: name,
        desc: description,
        imageurl: image,
        price: price,
        qtty: 0,
        category: category,
      });
    },
    add_product_more(name, description, price, category, image) {
      console.log("add_product_more");
      var exists = false;
      if (category == i18n.t("newUi.addItem.noCate")) {
        category = "";
      }
      if (name == null || name == 0 || name == "") {
        alert(i18n.t("alertText.itemNameAlert"));
      } else if (name.length > 100) {
        alert(i18n.t("alertText.itemMax"));
      } else if (parseFloat(price) < 0 || !parseFloat(price) || price == "") {
        alert(i18n.t("alertText.validatePriceAlert"));
      } else if (parseFloat(price) > 999999) {
        alert(i18n.t("alertText.pricelimit"));
      } else {
        var decimal_exists = false;
        var decimal = price.split(".")[1];
        if (decimal) {
          if (decimal.length > 2) {
            decimal_exists = true;
          }
        }
        this.$store.state.currentForm.catalogue.map((each_category) => {
          if (each_category.type == "item") {
            if (
              each_category.name.toLowerCase().replace(/ /g, "") ===
              name.toLowerCase().replace(/ /g, "")
            ) {
              exists = true;
            }
          }
        });
        if (exists) alert(i18n.t("alertText.productExist"));
        else if (decimal_exists) alert(i18n.t("alertText.roundOffDigits"));
        else {
          this.sheet = false;
          this.additem_more(name, description, price, category, image);
          this.saveCurrentFormMore();
          this.set_product_item_null();
          this.sheet = true;
        }
      }
    },
    add_product_submit(name, description, price, category, image) {
      var exists = false;
      if (category == i18n.t("newUi.addItem.noCate")) {
        category = "";
      }
      if (name == null || name == 0 || name == "") {
        alert(i18n.t("alertText.itemNameAlert"));
      } else if (name.length > 100) {
        alert(i18n.t("alertText.itemMax"));
      } else if (parseFloat(price) < 0 || !parseFloat(price) || price == "") {
        alert(i18n.t("alertText.validatePriceAlert"));
      } else if (parseFloat(price) > 999999) {
        alert(i18n.t("alertText.pricelimit"));
      } else {
        var decimal_exists = false;
        var decimal = price.split(".")[1];
        if (decimal) {
          if (decimal.length > 2) {
            decimal_exists = true;
          }
        }
        this.$store.state.currentForm.catalogue.map((each_category) => {
          if (each_category.type == "item") {
            if (
              each_category.name.toLowerCase().replace(/ /g, "") ===
              name.toLowerCase().replace(/ /g, "")
            ) {
              exists = true;
            }
          }
        });
        if (exists) alert(i18n.t("alertText.productExist"));
        else if (decimal_exists) alert(i18n.t("alertText.roundOffDigits"));
        else {
          this.sheet = false;
          this.additem(name, description, price, category, image);
        }
      }
    },
    checkLength(event) {
      if (event.length > 100) {
        this.addproduct_name = event.slice(0, -1);
        alert(i18n.t("alertText.itemMax"));
        event = event.slice(0, -1);
      } else if (event.length <= 100) {
        this.addproduct_name = event;
      }
    },
    set_product_item_null() {
      this.addproduct_desc = "";
      this.addproduct_image = "";
      this.addproduct_name = "";
      this.addproduct_price = "";
      this.data_saved = false;
    },
    submit: function() {
      this.sheet = true;
    },
    cancel: function() {
      router.push("/admin/viewform/" + this.$store.state.currentForm.pos);
    },
    saveCurrentForm: function() {
      // var shopname = this.$store.state.currentForm.shopname;
      // var phone = this.$store.state.currentForm.phone;
      if (
        this.addproduct_name == null ||
        this.addproduct_name.length == 0 ||
        this.addproduct_name == ""
      ) {
        alert(i18n.t("alertText.validateNameAlert", { type: "item" }));
      } else if (
        parseFloat(this.addproduct_price) < 0 ||
        !parseFloat(this.addproduct_price) ||
        this.addproduct_price == ""
      ) {
        alert(i18n.t("alertText.validatePriceAlert"));
      } else {
        this.$store.dispatch("setOverlay", true);
        this.set_overlay = true;
        var elethis = this;
        console.log("Call 1");
        // debugger;
        saveFile(
          this.$store.state.profile.uid,
          this.$store.state.currentFormPos,
          JSON.stringify(this.$store.state.currentForm),
          function(error) {
            if (error) {
              //TODO skeleton data
              alert(i18n.t("alertText.saveFailed"));
              store.dispatch("setOverlay", false);
              elethis.set_overlay = false;
            } else {
              // alert(i18n.t("alertText.uploadText"));
              store.dispatch("setOverlay", false);
              elethis.set_overlay = false;
            }
          }
        )
          .then((resp) => {
            saveMasterData(function(err) {
              console.log(err);
            }, this.$store.state);
            this.data_saved = true;

            var elethis = this;
            if (
              router.currentRoute.fullPath ==
              "/admin/editform/" + elethis.$store.state.currentForm.pos
            ) {
              router.push(
                "/admin/viewform/" + elethis.$store.state.currentForm.pos
              );
            } else if (
              router.currentRoute.fullPath ==
              "/admin/additemstoform/" + elethis.$store.state.currentForm.pos
            ) {
              router.push(
                "/admin/viewform/" + elethis.$store.state.currentForm.pos
              );
            }
          })
          .catch((err) => {
            console.log("SSI Error");
          });
      }

      console.log("Call ");
    },
    saveCurrentFormMore: function() {
      // var shopname = this.$store.state.currentForm.shopname;
      // var phone = this.$store.state.currentForm.phone;
      if (
        this.addproduct_name == null ||
        this.addproduct_name.length == 0 ||
        this.addproduct_name == ""
      ) {
        alert(i18n.t("alertText.validateNameAlert", { type: "item" }));
      } else if (
        parseFloat(this.addproduct_price) < 0 ||
        !parseFloat(this.addproduct_price) ||
        this.addproduct_price == ""
      ) {
        // console.log(this.price, parseFloat(this.price))
        alert(i18n.t("alertText.validatePriceAlert"));
      } else {
        this.$store.dispatch("setOverlay", true);
        this.set_overlay = true;
        var elethis = this;
        saveFile(
          this.$store.state.profile.uid,
          this.$store.state.currentFormPos,
          JSON.stringify(this.$store.state.currentForm),
          function(error) {
            if (error) {
              //TODO skeleton data
              alert(i18n.t("alertText.saveFailed"));
              store.dispatch("setOverlay", false);
              elethis.set_overlay = false;
            } else {
              // alert(i18n.t("alertText.uploadText"));
              store.dispatch("setOverlay", false);
              elethis.set_overlay = false;
            }
          }
        );
        saveMasterData(function(err) {
          console.log(err);
        }, this.$store.state);
      }
    },
    crop() {
      // debugger;
      let options = {
        type: "base64",
        format: "jpeg",
        circle: false,
      };
      let elethis = this;
      elethis.set_overlay = true;
      var uid = this.$store.state.profile.uid;
      this.$refs.croppieRef.result(options).then(function(output) {
        elethis.cropped = output;
        // console.log(output);
        elethis.modalToggle("none");

        saveImage(uid, new Date().getTime(), output, function(error, newurl) {
          if (error) {
            alert(i18n.t("alertText.uploadFail"));
          } else {
            elethis.addproduct_image = newurl;
          }
          setTimeout(() => {
            elethis.set_overlay = false;
          }, 1500);
        });
      });
    },
    crop1() {
      // debugger;
      // Here we are getting the result via callback function
      // and set the result to this.cropped which is being
      // used to display the result above.
      let options = {
        format: "jpeg",
        circle: true,
      };
      this.$refs.croppieRef.result(options, (output) => {
        this.cropped = output;
      });
    },
    result(output) {
      this.cropped = output;
    },
    update(val) {
      this.d = val;
    },
    rotate(rotationAngle) {
      // Rotates the image
      this.$refs.croppieRef.rotate(rotationAngle);
    },
    uploading: function(name) {
      var elementThis = this;
      const input = document.createElement("input");
      const elethis = this;
      input.setAttribute("type", "file");
      input.addEventListener("change", function() {
        if (name == "mobile") {
          //elethis.cropDialog = true;
          elementThis.modalToggle("block");
        } else if (name == "desktop") {
          //elethis.cropDialog2 = true;
          elementThis.modalToggle("block");
        }
        setTimeout(() => {
          elethis.$refs.croppieRef.bind({
            url: window.URL.createObjectURL(input.files[0]),
            zoom: 0.1,
          });
        }, 200);
      });
      input.click();
      return false; // avoiding navigation
    },
  },
};
</script>
<style lang="scss">
.text_color {
  color: #8ab26d;
  font-weight: bold;
}
.icon_color {
  color: #8ab26d;
  justify-content: center;
  font-size: 20px;
  width: 26px;
  height: 26px;
  background-color: #f8faf7;
  padding: 5px;
  margin: 3px;
  border-radius: 3px;
}
.button_padding {
  margin-right: 10px;
  color: white !important;
  padding: 10px;
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 0px;
  text-transform: none;
  text-align: center;
  cursor: pointer;
}

.cr-slider-wrap {
  display: none;
}

.bottom_modal {
  border-top-right-radius: 20px !important;
  border-top-left-radius: 20px !important;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
input[type="number"] {
  margin-bottom: 0px;
  border: none;
  margin-top: 0px;
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 111; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}

/* Modal Content */
.modal-content {
  background-color: rgba(0, 0, 0, 0.4);
  margin: auto;
  width: 100%;
  height: 100%;
}

/* The Close Button */
.close {
